<template>
  <div class="wrap">
    <h2 class="el-icon-coffee">ÂçöÂÆ¢‰ø°ÊÅØ</h2>
    <ul>
      <li class="count el-icon-coffee-cup">ÊñáÁ´†ÊÄªÊï∞ <span>{{allArticleData}}</span></li>
      <li class="el-icon-user">ÊúÄËøëËÆøÂÆ¢ <span>{{visitorData}}</span> ‰Ωç</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "Bloginfo",
  data(){
    return{
      visitorData:0,
      allArticleData:0
    }
  },
  methods:{
    async getVisitor() {
      let { data } = await this.$axios({
        method: "GET",
        url: "/get/visitor",
      });
      if (data.code) return; //Ê≤°ÊúâËÆøÂÆ¢Êó∂Áõ¥Êé•return
      // console.log(data.data.length );
      this.visitorData = data.data.length;
    },
    async getArticles(){
      let {data} = await this.$axios({
        method:"GET",
        url:"/get/article"
      });
      // console.log(data);
      if(data.code) return this.$message.error(data.message);//Ê≤°ÊúâÊñáÁ´†Êó∂/Êä•ÈîôÔºåÁõ¥Êé•return
      this.allArticleData = data.data.length; //ÊâÄÊúâÊñáÁ´†Êï∞ÊçÆÁªôallArticleData
    },       
  },
  created(){
    this.getVisitor();
    this.getArticles()
  }
};
</script>

<style lang="less" scoped>
h2 {
  position: relative;
  left: 10px;
}
ul {
  margin-top: 10px;
  li {
    padding-left: 10px;
    &:nth-of-type(1){
        border-bottom: 1px solid #e0e0e0;
    }
  }
}
